  margin: 0.25rem 0.5rem;
  padding: 0.75rem 1rem;
  color: var(--text-muted, #666679);
  font-size: 0.75rem;
  font-weight: 700;
  text-transform: uppercase;
}

details summary.mcc-menu__subtitle {
  display: flex;
  align-items: center;
}
details summary.mcc-menu__subtitle mcc-icon[name=angle_down] {
  display: flex;
