.mcc-tooltip {
  position: relative;
}

.mcc-tooltip::after {
  position: absolute;
  z-index: 1000000;
  display: none;
  padding: 0.25rem 0.5rem;
  font-size: 0.75rem;
  color: var(--text-inverse, #fff);
  text-align: center;
  text-decoration: none;
  text-shadow: none;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: break-word;
  white-space: pre;
  pointer-events: none;
  content: attr(aria-label);
  background: var(--container-bg-inverse, #161616);
  border-radius: 9px;
  opacity: 0;
}

.mcc-tooltip::before {
  position: absolute;
  z-index: 1000001;
  display: none;
  width: 0;
  height: 0;
  color: var(--container-bg-inverse, #161616);
  pointer-events: none;
  content: "";
  border: 6px solid transparent;
  opacity: 0;
}

@keyframes tooltip-appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.mcc-tooltip:hover::before, .mcc-tooltip:hover::after,
.mcc-tooltip:active::before,
.mcc-tooltip:active::after,
.mcc-tooltip:focus::before,
.mcc-tooltip:focus::after {
  display: inline-block;
  text-decoration: none;
  animation-name: tooltip-appear;
  animation-duration: 110ms;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in;
  animation-delay: 400ms;
}

.mcc-tooltip--no-delay:hover::before, .mcc-tooltip--no-delay:hover::after,
.mcc-tooltip--no-delay:active::before,
.mcc-tooltip--no-delay:active::after,
.mcc-tooltip--no-delay:focus::before,
